// Publiseringsvariabler
//
$editModeSprite_url: "../images/editModeSprite.svg";


// Publisering
// -----------------------------------------------------------------------------

// Sprite icon collection
.icon.small_edit_icon {
    background-image: url($editModeSprite_url);
    background-position: -160px -192px;
}
.icon.align_left_icon {
    background-image: url($editModeSprite_url);
    background-position: 0 -64px;
}
.icon.align_center_icon {
    background-image: url($editModeSprite_url);
    background-position: 0 -96px;
}
.icon.align_right_icon {
    background-image: url($editModeSprite_url);
    background-position: -96px -64px;
}
.icon.preview_icon {
    background-image: url($editModeSprite_url);
    background-position: -64px -128px;
}


